import React, { CSSProperties, useState } from "react";
import Graph from "./Graph";

import { useCSVReader } from "react-papaparse";

const styles = {
  csvReader: {
    display: "flex",
    flexDirection: "row",
    marginBottom: 10,
  },
  browseFile: {
    width: "20%",
  },
  acceptedFile: {
    border: "1px solid #ccc",
    height: 45,
    lineHeight: 2.5,
    paddingLeft: 10,
    width: "80%",
  },
  remove: {
    borderRadius: 0,
    padding: "0 20px",
  },
  progressBarBackgroundColor: {
    backgroundColor: "red",
  },
};

export default function CSVReader() {
  const { CSVReader } = useCSVReader();
  const [data, setData] = useState([]);

  const data_64 = data.filter((e) => e.block_size == 64);
  const data_256 = data.filter((e) => e.block_size == 256);
  const data_4096 = data.filter((e) => e.block_size == 4096);

  return (
    <>
      <CSVReader
        onUploadAccepted={(results) => {
          setData(results.data);
        }}
        config={{ header: true }}
      >
        {({ getRootProps, acceptedFile, ProgressBar, getRemoveFileProps }) => (
          <>
            <div style={styles.csvReader}>
              <button
                type="button"
                {...getRootProps()}
                style={styles.browseFile}
              >
                Browse file
              </button>
              <div style={styles.acceptedFile}>
                {acceptedFile && acceptedFile.name}
              </div>
              <button {...getRemoveFileProps()} style={styles.remove}>
                Remove
              </button>
            </div>
            <ProgressBar style={styles.progressBarBackgroundColor} />
          </>
        )}
      </CSVReader>
      <div
        style={{
          // border: "1px solid green",
          display: "flex",
          flexWrap: "wrap",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "10px",
        }}
      >
        <Graph data={data_64} title="Block Size: 64" />
        <Graph data={data_256} title="Block Size: 256" />
        <Graph data={data_4096} title="Block Size: 4096" />
      </div>
    </>
  );
}
